import React, {useEffect} from 'react';
import { capitalizeFirstLetter } from '../../utils/helpers';


function Nav(props) {
    const {
      categories = [],
      setCurrentCategory,
      contactSelected,
      currentCategory,
      setContactSelected,
    } = props;

    useEffect(() => {
        document.title = capitalizeFirstLetter(currentCategory.name);
      }, [currentCategory]);

    return (    
    <header className="flex-row px-1">
      <h4>
        <a data-testid="link" href="https://mohlenkamp.github.io/portfolio-spa/">
          <span role="img" aria-label="portfolio"><img className="icon" alt="portfolio-icon" src={require(`../../assets/img/portfolio.png`).default}/> </span> John Mohlenkamp Portfolio
        </a>
      </h4>
      <nav>
        <ul className="flex-row">
          {/* <li className="mx-2">
            <a data-testid="about" href="#about" onClick={() => setContactSelected(false)}>
              About Me
            </a>
          </li> */}
          {/* <li className={`mx-2 ${contactSelected && 'navActive'}`}>
            <span onClick={() => setContactSelected(true)}>Contact</span>
          </li> */}
          {categories.map((category) => (
            <li
              className={`mx-1 ${
                currentCategory.name === category.name && !contactSelected && 'navActive'
                }`}
              key={category.name}
            >
              <span
                onClick={() => {
                  setCurrentCategory(category);
                  console.log(category.name);
                  setContactSelected(false);
                }}
              >
                {capitalizeFirstLetter(category.name)}
              </span>
            </li>
          ))}
        </ul>
      </nav>
    </header>
    );
}

export default Nav;